:root {
    --color1: #373331;
    --color2: #605a56;
    --color3: #80ac7b;
    --color4: #e8eaa1;
    --color5: #433e3c;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Inconsolata", monospace;
    outline: none;
}

html {
    height: 100%;
    font-family: "Inconsolata", monospace;
}

body {
    min-height: 100%;
    overflow: hidden;
    background-color: var(--color1);
}

.app {
    width: 100%;
    margin: auto;
    overflow: hidden;
}
