.map {
    overflow: hidden;
    margin: auto;
    width: 100%;
}

.rsm-svg {
    display: block;
    height: 100vh;
    width: 100%;
}

.rsm-svg path {
    fill: var(--color2);
    stroke: var(--color4);
    stroke-width: 0.4px;
    transition: fill 0.5s;
    cursor: pointer;
}

.incorrect-country {
    fill: var(--color5);
}

.correct-country {
    fill: var(--color3) !important;
    pointer-events: none;
}

@media screen and (max-width: 1000px) {
    .rsm-svg {
        width: unset;
    }
}
