.quiz-header {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    background-color: var(--color5);
    color: var(--color3);
    width: 40%;
    min-width: 300px;
    height: 52px;
    justify-content: space-around;
    font-size: 2em;
    border-bottom: 2px solid var(--color3);
    align-items: center;
}

.timer,
.title,
.score,
.spacer {
    margin-top: -2px;
}

.left-triangle,
.right-triangle {
    position: absolute;
    border-right: 54px solid var(--color3);
    border-bottom: 54px solid transparent;
    border-left: 54px solid transparent;
    left: -108px;
}

.right-triangle {
    border-right-color: transparent;
    border-left-color: var(--color3);
    left: unset;
    right: -108px;
}

.left-triangle::before,
.right-triangle::before {
    content: "";
    position: absolute;
    border-right: 50px solid var(--color5);
    border-bottom: 50px solid transparent;
    border-left: 50px solid transparent;
    right: -54px;
    top: 2px;
}

.right-triangle::before {
    border-right-color: transparent;
    border-left-color: var(--color5);
    left: unset;
    left: -54px;
}

@media screen and (max-width: 700px) {
    .spacer {
        display: none;
    }
}
