.guess-area {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: var(--color5);
    padding: 20px;
    display: flex;
    gap: 25px;
    height: 80px;
    justify-content: center;
    align-items: flex-end;
}

.img-endgame-screen-container {
    width: 20%;
}

.country-flag-img {
    display: block;
    width: 100%;
}

.guess-area--input-container {
    position: relative;
}

.guess-area--input {
    width: 100%;
    padding: 5px;
    font-size: 1.5em;
    outline: none;
    background-color: var(--color2);
    border: none;
    color: var(--color3);
    text-align: center;
}

.input-indicator {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 100%;
    background-color: var(--color4);
    transform-origin: left;
    transform: scaleX(0);
    transition: transform 0.5s, background-color 1s;
}

.guess-area--input:focus + .input-indicator {
    transform: scaleX(1);
}

.guess-area--button {
    font-size: 1.5em;
    padding: 5px 15px;
    background-color: var(--color1);
    border: none;
    color: var(--color4);
    cursor: pointer;
    transition: background-color 0.5s, color 0.5s;
}

.guess-area--button:hover {
    background-color: var(--color2);
}

.guess-area--button:disabled {
    pointer-events: none;
    color: #8b8b8b;
}

.guess-area--buttons {
    display: flex;
    gap: 20px;
}

.endgame-screen {
    width: 100%;
    height: 150px;
    font-size: 1.5em;
    text-align: center;
    padding-top: 37.5px;
}

@media screen and (max-width: 900px) {
    .guess-area {
        flex-direction: column;
        height: 240px;
        align-items: center;
        width: auto;
        left: 50%;
        transform: translateX(-50%);
        justify-content: flex-end;
    }

    .img-endgame-screen-container {
        width: 100%;
    }
}
